<template>
  <div>
    <div>
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <div>
            <iframe width="100%" height="300" frameborder="0" scrolling="auto"
                    :src="url"></iframe>
          </div>
        </el-col>
      </el-row>
    </div>


    <div style="margin-top: 50px;">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <el-select v-model="value" placeholder="请选择接口" style="width: 100%;">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-col>
      </el-row>
    </div>

    <div style="margin-top: 10px;">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <div>
            <el-input v-model="input" placeholder="请输入视频链接"></el-input>
          </div>
        </el-col>
      </el-row>
    </div>

    <div style="margin-top: 10px;">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24">
          <div>
            <el-button type="primary" style="width: 100%;" @click="onClick">确定</el-button>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        url: ``,
        input: ``,
        options: [{
          value: 'https://jx.618g.com/?url=',
          label: '接口1'
        }, {
          value: 'http://74t.top/?url=',
          label: '接口2'
        }, {
          value: 'http://mimijiexi.top/?url=',
          label: '接口3'
        }, {
          value: 'http://55jx.top/?url=',
          label: '接口4'
        }, {
          value: 'https://api.sigujx.com/?url=',
          label: '接口5'
        }],
        value: 'https://jx.618g.com/?url='
    }
  }
  ,
  components: {
  }
  ,
  methods: {
    onClick()
    {
      this.url = this.value + this.input;
    }
  }
  ,
  computed: {
  }
  ,
  mounted()
  {

  }
  }
</script>


<style type="text/css" scoped>

</style>



